.topHeader {
    background-color: $accent;
    color: $color;
    height: rem(60);

    nav {
        text-align: center;
        font-size: rem(12);
        ul {
            li {
                height: 100%;
                a {
                    text-decoration: none; } } } } }




.textCenter {
    text-align: center; }


.middleHeader {
    height: rem(100px);
    // background-color: #1477A6
    color: lighten($color-white, 10);
    font-weight: bold;
    box-shadow: 0 10px 6px -6px #C0C0C080;

    .description-phone a {
        text-decoration: none;
        display: table;
        font: normal 2rem  sans-serif;
        font-weight: normal;
        line-height: normal;
        font-weight: bold;
        line-height: rem(32px);
        border-bottom: 1px dashed; }







    .col {
        padding: 0;
        text-align: center; } }


.bottomHeader {
    // border-bottom: 0.2em solid lighten($color, 1)
    padding: 0.3em 0;

    &_container {
        align-items: center;

        color: $color;
        display: flex;
        font: #{$font-size}/1.375 'Lato', arial, sans-serif;
        flex-flow: column nowrap;
        justify-content: center; }
    .cat {
        height: 5em;
        ul {
            width: 100%;
            height: 2rem;
            display: flex;
            justify-content: space-around;
            align-items: center;
            color: $color-white;
            li {
                margin-left: 0;
                a {}
                text-decoration: none; } } } }







