/*----------------------------
 *    Calltoaction Styles
 *------------------------------ */

.tc-calltoaction {
  padding: 25px rem(30px);
  background: {}
  min-height: rem(63px);
  border: 1px solid rgba($accent, 0.04);
  -webkit-box-shadow: -3px 0 0 rgba($accent, 0.8);
  box-shadow: -3px 0 0 rgba($accent, 0.8);
  .cta-button {
    display: inline-block;
    padding: 20px rem(30px);
    font-size: rem(16px);
    text-decoration: none;
    background-color: $primary;
    color: $color;
    &:hover {
      background-color: $colorHover;
      color: $color; } }
  h3 {
    font-size: rem(22px);
    line-height: rem(32px);
    margin: 0;
    color: $color-white; }
  p {
    margin: 5px 0 0; }
  &:not(.cta-align-center) {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -ms-flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center; }
  &.cta-align-left {
    text-align: left;
    .cta-button {
      margin-left: auto; } }
  &.cta-align-right {
    text-align: right;
    -webkit-box-shadow: 3px 0 0 rgba($accent, 0.8);
    box-shadow: 3px 0 0 rgba($accent, 0.8); }
  &.cta-align-center {
    text-align: center;
    padding: rem(45px);
    -webkit-box-shadow: 0 -3px 0 rgba($accent, 0.8);
    box-shadow: 0 -3px 0 rgba($accent, 0.8);
    h3 {
      font-size: rem(26px); }
    .cta-button {
      margin-top: rem(25px); }
    .cta-content > div {
      margin-top: rem(10px); } }
  &.cta-radius {
    padding: 25px rem(40px);
    border-radius: rem(60px);
    -webkit-box-shadow: none;
    box-shadow: none;
    .cta-button {
      border-radius: rem(60px); } } }

/*----------------------------
 *    Responsive Medium
 *------------------------------ */
@media (min-width: 767px) {
  .tc-calltoaction {
    &:not(.cta-align-center) .cta-content {
      max-width: 75%; }
    &.cta-align-right {
      a.cta-button {
        -ms-flex-order: -1;
        -webkit-order: -1;
        order: -1; }
      .cta-content {
        margin-left: auto; } } } }

@media only screen and (min-width: 768px) and (max-width: 979px) {
  .tc-calltoaction:not(.cta-align-center) .cta-content {
    max-width: 70%; } }

@media (max-width: 767px) {
  .tc-calltoaction {
    .cta-button {
      margin: 18px 0 rem(10px);
      text-align: center; }
    .cta-content {
      text-align: center;
      margin-left: auto;
      margin-right: auto; }
    &.cta-align-left .cta-button {
      margin-right: auto; }
    &.cta-align-right .cta-button {
      margin-right: auto;
      margin-left: auto; } } }

@media only screen and (max-width: 480px) {
  .tc-calltoaction {
    .cta-button {
      padding: rem(10px);
      margin: 18px 0 rem(10px);
      font-size: rem(14px);
      text-align: center; }
    .cta-content {
      text-align: center; } } }
