.slider-s {
  width: 100%;
  max-width: 50%;
  margin: 0 auto;
  position: relative;
  img {
      width: 100%; }
  > .prev {
    position: absolute;
    top: 50%;
    background-color: transparent;
    color: #1477a6;
    opacity: .8; } }

.next {
  position: absolute;
  top: 50%;
  background-color: transparent;
  color: #1477a6;
  opacity: .8; }

.slider-s {
  > {
    .prev {
      position: absolute;
      left: 0;
      transform: translate(8%, -50%); }
    .next {
      position: absolute;
      right: 0;
      transform: translate(-8%, -50%); } } }

