$font-size: rem(14px);
$icon-size: rem(20px);
$width-input: 100%;
$border-radius: rem(4px);
$transition: all 0.5s;


// //////////////////////////////////////////////
//
// 1 - Standard font of site: NotoSans
//
// //////////////////////////////////////////////

// Font weights


$light: 300;
$regular: 400;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;

$base-font-size: 16px;
$base-line-height: 1.5;
$notosans: 'NotoSans', Helvetica, Arial, sans-serif;
$main-font-family: $notosans;
$fzNav: 0.75rem;

// ////////////////////////////////////////////////////////////
//
// 2 - colors
//
// ///////////////////////////////////////////////////////////

// Colors

$primary: #1477a6;
$primary-dark: darken(desaturate($primary, 10), 5);
$accent: #242c38;
$primary-alfa: rgba($accent, .3);
$colorHover: lighten($primary, 10);
$colorActive: lighten($primary, 6);

$color: #ECF0F1;
$color-white: #242c38;
$color-dark: darken($color, 20%);

// ////////////////////////////////////////////////////////////
//
// 3 - boxShadow
//
// ///////////////////////////////////////////////////////////

$bxs: 0 2px 2px 0 rgba(0,0,0,0.14),0 1px 5px 0 rgba(0,0,0,0.12),0 3px 1px -2px rgba(0,0,0,0.2);


$w-h: 20px;
