
/*--------------------------------
 *    Common Styles
 *---------------------------------- */

.heading-inner {
  position: relative;
  display: inline-block;
  margin-bottom: rem(20);
  margin-top: rem(20);
  font-size: rem(20);
  text-transform: uppercase;
  text-rendering: optimizeLegibility; }

/*--------------------------------
 *    Heading Style1
 *---------------------------------- */

.heading-style {
  .heading-inner {
    padding-bottom: rem(10px);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    &:after {
      content: '';
      position: absolute;
      height: rem(1px);
      width: 25%;
      bottom: -1px;
      left: 0;
      background-color: $primary; } }
  &.heading-center {
    text-align: center;
    .heading-inner:after {
      left: 50%;
      -webkit-transform: translateX(-50%);
      transform: translateX(-50%); } }
  &.heading-right {
    text-align: right;
    .heading-inner:after {
      left: auto;
      right: 0; } } }


/*--------------------------------
 *    Heading Sizes
 *---------------------------------- */

.heading-xlarge .heading-inner {
  font-size: rem(42px); }

.heading-large .heading-inner {
  font-size: rem(36px); }

.heading-small .heading-inner {
  font-size: rem(17px); }
