/*Button One */

[class*="-one"] {
  padding: 20px 60px;
  outline: none;
  // box-shadow: 0 9px #95a5a6
  &:hover {
    background-color: $colorHover; }
  &:active {
    background-color: $colorActive;
    box-shadow: 0 5px #95a5a6;
    transform: translateY(4px); } }

/*Button Two */

[class*="-two"] {


  span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
    &:after {
      content: 'Â»';
      position: absolute;
      opacity: 0;
      top: 0;
      right: -20px;
      transition: 0.5s; } }
  &:hover span {
    padding-right: 25px;
    &:after {
      opacity: 1;
      right: 0; } } }

/*Button Three */

[class*="-light"] {
  position: relative;
  transition-duration: 0.4s;
  overflow: hidden;
  &:hover {
    background: $colorHover+10;
    box-shadow: 0px 2px 10px 5px #97B1BF;
    color: lighten($color-dark, 20); }
  &:after {
    content: "";
    background: $primary+20;
    display: block;
    position: absolute;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -20px !important;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s; }
  &:active:after {
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s; } }



