/*-----------------------
 *    Common Styles
 *------------------------- */

[class*="tc-link"] {
  padding: 0;
  margin: 0;
  ul {
    padding: 0;
    margin: 0;
    height: 100%; }
  li {
    display: inline-block;
    list-style-type: none;
    + li {
      margin-left: 15px; }
    a {
      display: block;
      position: relative;
      color: $color  $color;
      text-transform: uppercase;
      font-weight: $semi-bold;
      -webkit-transition: all .25s ease;
      transition: all .25s ease;
      &:hover {
        color:  $color-dark; } }
    &.active a {
      color:  $color-dark; } } }

/*-----------------------
 *    Link Effect1
 *------------------------- */

.tc-link-effect1 li {
  a {
    position: relative;
    padding-bottom: 8px;
    &::after {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 3px;
      background: rgba(0, 0, 0, 0.1);
      content: '';
      opacity: 0;
      -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
      transition: opacity 0.3s, transform 0.3s;
      -webkit-transform: translateY(10px);
      transform: translateY(10px); }
    &:hover::after {
      opacity: 1;
      -webkit-transform: translateY(0px);
      transform: translateY(0px); } }
  &.active a:after {
    opacity: 1;
    -webkit-transform: translateY(0px);
    transform: translateY(0px); } }

/*-----------------------
 *    Link Effect2
 *------------------------- */

.tc-link-effect2 li {
    a {
        position: relative;
        padding-bottom: 2px;
        &::after {
            position: absolute;
            top: 100%;
            left: 0;
            width: 0;
            height: 1px;
            background:  $color-dark;
            content: '';
            opacity: 0;
            -webkit-transition: opacity 0.3s, width 0.4s;
            transition: opacity 0.3s, width 0.4s; }
        &:hover::after {
            opacity: 1;
            width: 30px; } }
    &.active a:after {
        opacity: 1;
        width: 30px; } }

/*-----------------------
 *    Link Effect4
 *------------------------- */

.tc-link-effect4 li {
  + li {
    margin-left: 15px; }
  a {
    &::before {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 2rem;
      height: 2rem;
      border: 2px solid rgba($accent, 0.05);
      border-radius: 50%;
      content: '';
      opacity: 0;
      -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
      transition: transform 0.3s, opacity 0.3s;
      -webkit-transform: translateX(-50%) translateY(-50%) scale(0.2);
      transform: translateX(-50%) translateY(-50%) scale(0.2); }
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 2rem;
      height: 2rem;
      border: 2px solid rgba($primary, 0.07);
      border-radius: 50%;
      content: '';
      opacity: 0;
      -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
      transition: transform 0.3s, opacity 0.3s;
      -webkit-transform: translateX(-50%) translateY(-50%) scale(0.2);
      transform: translateX(-50%) translateY(-50%) scale(0.2);
      width: 70px;
      height: 70px;
      border-width: 6px;
      -webkit-transform: translateX(-50%) translateY(-50%) scale(0.8);
      transform: translateX(-50%) translateY(-50%) scale(0.8); }
    &:hover {
      &::before, &::after {
        opacity: 1;
        -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
        transform: translateX(-50%) translateY(-50%) scale(1); } } }
  &.active a {
    &:before, &:after {
      opacity: 1;
      -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
      transform: translateX(-50%) translateY(-50%) scale(1); } } }
