.gridWrap {
  display: grid;
  grid-auto-rows: rem(400);
  grid-gap: rem(30);
  grid-template-columns: repeat(3, minmax(200px, 300px));
  justify-content: center;
  align-items: stretch;
  padding-top: rem(20px);
 }  // min-height: 100vh

@media screen and (max-width: 720px) {
  .container {
    grid-template-columns: 1fr;
    margin: 50px 30px; } }

.card {
  position: relative;
  padding: rem(10px);
  background: white;
  display: grid;
  grid-template-rows: 8fr 1fr 1fr;
  box-shadow: 0px 15px 20px 0px rgba(0, 0, 0, 0.057);
  transition: 0.2s ease-in;
  .card-content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    .top-bar {
      width: calc(100% - 40px);
      position: absolute;
      top: 0;
      left: 0;
      padding: rem(20px);
      .float-right {
        float: right; } }
    .img {
      width: 100%;
      justify-content: bottom;
      align-content: bottom;
      text-align: center;
      img {
        max-width: 100%;
        max-height: rem(220px); } } }
  .card-description {
    padding: rem(10px);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 400;
    font-size: rem(14px);
    letter-spacing: 0.1em;
    width: calc(100%-20px);
    .title {
      text-transform: uppercase;
      text-align: left; }
    .cart {
      float: right;
      cursor: pointer; } }
  .card-footer {
    text-transform: uppercase;
    padding: 0.3em;
    border-top: 0.5px solid #ececec;
    letter-spacing: 0.1em;
    font-size: rem(11px);
    color: white;
    justify-items: left;
    align-items: center;
    display: flex;
    .span {
      margin: rem(5px);
      width: auto;
      background: #dedede;
      padding: 4px rem(7px);
      border-radius: rem(2px);
      font-weight: 100;
      cursor: pointer;
      &:hover {
        background: #aaaaaa; } } }
  &:hover {
    transition: 0.2s ease-in;
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
    box-shadow: 0px 45px 60px 0px rgba(0, 0, 0, 0.070); } }
