





// Rules
.bottomHeader {
    // border-bottom: 0.2em solid lighten($color, 1)
    padding: 0.3em 0;

    &_container {
        align-items: center;

        color: #fff;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center; }


    .note {
        font-size: 0.700em;
        font-weight: bold;
        text-transform: uppercase;
        color: darken($primary-dark, 15); }

    #search {
        align-items: center;
        background: $primary;
        border-radius: $border-radius;
        display: flex;
        justify-content: space-between;
        // margin: 0.5em 0
        padding: 0.5em 0.5em 0.5em 1em;
        font: #{$font-size}/1.375 'Lato', arial, sans-serif;
        transition: $transition;
        width: $width-input;

        &:hover,
        &:focus {
            background: darken($primary, 2); }

        button,
        input {
            appearance: none;
            background: transparent;
            border: 0;
            color: inherit;
            font: inherit;
            outline: 0; }

        button {
            cursor: pointer;
            padding: 0 0.25em; }

        input {
            flex: 1;

            &::placeholder {
                color: #fff; } }

        .spinner {
            animation: spinner 1s infinite linear;
            display: none;
            padding: 0 0.25em; } } }

#search.loading {
	button {
		display: none; }

	.spinner {
		display: block; } }


// Animation

@keyframes spinner {
	0% {
		transform: rotate(0deg); }

	100% {
		transform: rotate(360deg); } }
