/* middleHeader search form */
.topHeader_form {
  position: absolute;
  margin: auto;
  top: 50%;
  left: 90%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  // right: 0
  // bottom: 0
  width: 300px;
  height: 100px;
  .search {
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background: crimson;
    border-radius: 50%;
    transition: all 1s;
    z-index: 4;
    box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.4);
    &:hover {
      cursor: pointer; }
    &::before {
      content: "";
      position: absolute;
      margin: auto;
      top: 22px;
      right: 0;
      bottom: 0;
      left: 22px;
      width: 12px;
      height: 2px;
      background: white;
      transform: rotate(45deg);
      transition: all .5s; }
    &::after {
      content: "";
      position: absolute;
      margin: auto;
      top: -5px;
      right: 0;
      bottom: 0;
      left: -5px;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      border: 2px solid white;
      transition: all .5s; } }
  input {
    font-family: 'Inconsolata', monospace;
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 50px;
    height: 50px;
    outline: none;
    border: none;
    background: crimson;
    color: white;
    text-shadow: 0 0 10px crimson;
    padding: 0 80px 0 20px;
    border-radius: 30px;
    box-shadow: 0 0 25px 0 crimson, 0 20px 25px 0 rgba(0, 0, 0, 0.2);
    transition: all 1s;
    opacity: 0;
    z-index: 5;
    font-weight: bolder;
    letter-spacing: 0.1em;
    &:hover {
      cursor: pointer; }
    &:focus {
      width: 300px;
      opacity: 1;
      cursor: text;
      ~ .search {
        right: -250px;
        background: #151515;
        z-index: 6;
        &::before {
          top: 0;
          left: 0;
          width: 25px; }
        &::after {
          top: 0;
          left: 0;
          width: 25px;
          height: 2px;
          border: none;
          background: white;
          border-radius: 0%;
          transform: rotate(-45deg); } } }
    &::placeholder {
      color: white;
      opacity: 0.5;
      font-weight: bolder; } } }

/* Form contact */
.tc-form-style3 {
  color: $accent;
  .form-field {
    margin-bottom: 20px;
    input {
      background: transparent;
      width: 100%;
      height: 50px;
      padding: 5px 18px;
      font-size: 14px;
      border-width: 0 0 1px 0;
      border-style: solid;
      border-color: ($primary-alfa)*4; }
    textarea {
      background: transparent;
      width: 100%;
      padding: 10px 18px;
      font-size: 14px;
      border-width: 0 0 1px 0;
      border-style: solid;
      border-color: ($primary-alfa)*4; }
    input:focus, textarea:focus {
      border-color: $primary-alfa;
      outline: none; } }
  .form-button {
    text-align: center;
    button {
      background: transparent;
      border-width: 0 0 1px 0;
      border-style: solid;
      border-color: ($primary-alfa)*4;
      height: 46px;
      min-width: 170px;
      font-size: 14px;
      color: $accent;
      -webkit-transition: all .3s ease-out;
      transition: all .3s ease-out;
      &:hover {
        background: rgba($accent, 0.05);
        border-color: rgba($accent, 0.1);
        color: $colorHover; } } } }
